.formContainer{
  width:100%;
  height: auto;
  display: flex;
  justify-content: center;
}

.formContainer input{
  width: 40vw;
  height: 6vh;
  margin: 20px 10px;
}
.formContainer button{
  margin-bottom: 5px;;
}
.card{
  margin-bottom: 10px;
}
.card img{
  width:100%;
  max-height: 200px;
}
.card-body .card-title{
  font-size: 1.2rem;
}
.card-body .card-text{
  font-size: 0.9rem;
}
.navBar{
  width: 100vw;
  height: 7vh;
  background-color: rgb(19, 18, 18);
  display: flex;
  
}
.navBar .brand{
  color: white;
  padding: 3px 5px;
}
.navBar ul{
  display: flex;
  padding: 4px 5px;
}
.navBar ul li{
  display: flex;
  list-style: none;
  color: white;
  justify-content:space-around;
  margin:0px 5px;
}
.navBar ul li:hover{
  cursor: pointer;
}


